import React from 'react';
import { cn } from '@bem-react/classname';

import { Button } from '../Button/Button';

import './Modal.scss';
import { Card } from '../Card/Card';

const block = cn('Modal');

export const Modal = ({children, className, open}) => {
    if (!open) {
        return null;
    }

    return <div className={block()}>
        <Card className={block('ContentWrap', [className])}>{children}</Card>
    </div>
}

Modal.Header = ({className, children, onClose = () => {}}) => {
    return <div className={block('Header', [className])}>
        {children}
        <button className={block('BtnClose')} onClick={onClose}>x</button>
    </div>
}

Modal.Content = ({className, children}) => {
    return <div className={block('Content', [className])}>{children}</div>
}

Modal.Footer = ({className, children, onAccept, onDecline}) => {
    return <div className={block('Footer', [className])}>
        {children ? children : (
            <React.Fragment>
                <Button onClick={onAccept} view={'normal-flat'}>Подтверить</Button>
                <Button onClick={onDecline}>Отменить</Button>
            </React.Fragment>
        )}
    </div>
}
