import React, { useCallback } from 'react';
import { Api, api } from '../api/api';

const UserStateContext = React.createContext();
const UserUpdateContext = React.createContext();

const defaultState = {
    name: '',
    contactPhone: '',
    email: '',
    isAuth: false,
}

export function UserProvider({children}) {
    const [state, setState] = React.useState(defaultState);

    const update = useCallback(async () => {
        const response = await api.getUser();

        if (Api.isSuccess(response) && response.data) {
            setState((prevProps) => ({...prevProps, isAuth: true, ...response.data}));
            return true;
        }

        setState(defaultState);
        return false;
    }, [setState]);

    return (
        <UserUpdateContext.Provider value={update}>
            <UserStateContext.Provider value={state}>{children}</UserStateContext.Provider>
        </UserUpdateContext.Provider>
    );
}

function useUserState() {
    const state = React.useContext(UserStateContext);

    if (state === undefined) {
        throw new Error('useUserState must be used within a UserProvider');
    }

    return state;
}

function useUserUpdate() {
    const update = React.useContext(UserUpdateContext);

    if (update === undefined) {
        throw new Error('useUserUpdate must be used within a UserProvider');
    }

    return update;
}

export function useUser() {
    return [useUserState(), useUserUpdate()];
}