import React from 'react';
import { cn } from '@bem-react/classname';
import socketIOClient from "socket.io-client";
import { Link } from 'react-router-dom';

import { Api, api } from '../../api/api';
import { Card } from '../../components/Card/Card';
import { Header } from '../../components/Header/Header';
import { Menu } from '../../components/Menu/Menu';
import { useUser } from '../../context';
import { Layout } from '../../layouts/Layout/Layout';
import { getDate, sortChatByMessage, getReceiverFromChatUsers } from '../../utils';

import './Chats.scss';

const block = cn('Chats');

export const Chats = () => {
    const [innerChats, setInnerChats] = React.useState([])
    const [userState] = useUser();

    React.useEffect(() => {
        api.getChats().then((response) => {
            if (Api.isSuccess(response)) {
                const sortedData = response.data.sort(sortChatByMessage);
                setInnerChats(sortedData);
            }
        });
    }, []);

    React.useEffect(() => {
        const socket = socketIOClient({ query: `type=chats`, path: '/socket.io/chats' });
        socket.on("connect", () => {console.log('chats connected')});
        socket.on('disconnect', () => {console.log('chats disconnected')})

        socket.on("newChatsMessage", data => {
            if (Array.isArray(data)) {
                const sortedData = data.sort(sortChatByMessage);
                setInnerChats(sortedData);
            }
        });
    }, []);

    return (
        <Layout>
            <Menu />
            <Card className={block()}>
                <Header>Чаты</Header>
                <Card className={block('Chats')}>
                    {!innerChats.length ? <span>У вас нет чатов</span> : 
                        (
                            innerChats.map(chat => {
                                const receiver = getReceiverFromChatUsers(chat.users, userState.id)
                                const lastMessage = chat.messages[chat.messages.length - 1];
                                return (
                                    <Link key={chat.id} className={block('Link')} to={`/chats/${chat.id}`}>
                                        <Card className={block('Chat')}>
                                            <div className={block('Receiver')}>
                                                <span>{receiver.name}</span>
                                                <span>{lastMessage?.sentAt ? getDate(lastMessage.sentAt) : ''}</span>
                                            </div>
                                            <div className={block('Message')}>{lastMessage?.text ? `Last message: ${lastMessage?.text}` : 'Нет сообщений'}</div>
                                        </Card>
                                    </Link>
                                )
                            })
                        )}
                </Card>
            </Card>
        </Layout>
    )
}