import { getQueryFromObject } from "../utils";

class Api {
    static isSuccess = function (response) {
        return response.status === "ok";
    };

    commonOptions = {
        credentials: "include",
        mode: "cors",
        headers: {
            accept: "*/*",
        },
    };

    ws() {
        return new WebSocket("ws://localhost/ws");
    }

    request(url, options) {
        return fetch(url, { ...this.commonOptions, ...options }).then((res) =>
            res.json()
        );
    }

    get(url, options) {
        return this.request(url, options);
    }

    post(url, body) {
        const options = {
            body: JSON.stringify(body),
            method: "POST",
            headers: {
                ...this.commonOptions.headers,
            },
        }

        if (body instanceof FormData) {
            options.body = body;
        } else {
            options.headers['Content-Type'] = 'application/json';
        }

        return this.request(url, options);
    }

    delete(url) {
        return this.request(url, {
            method: 'DELETE',
        })
    }

    login(data) {
        return this.post("/api/signin", data);
    }

    logout() {
        return this.get("/api/logout");
    }

    getUser() {
        return this.get("/api/user");
    }

    getAdvertisements(filter) {
        let url = "/api/advertisements";
        if (filter) {
            url += `?${getQueryFromObject(filter)}`;
        }

        return this.get(url);
    }

    getAdvertisement(id) {
        return this.get(`/api/advertisements/${id}`);
    }

    creteAdvertisement(data) {
        return this.post("/api/advertisements", data);
    }

    createUser(data) {
        return this.post("/api/signup", data);
    }

    createChat(data) {
        return this.post('/api/chats', data);
    }

    getChats() {
        return this.get('/api/chats');
    }

    deleteAdvertisement(id) {
        return this.delete(`/api/advertisements/${id}`)
    }
}

const api = new Api();

export { api, Api };
