import { cn } from '@bem-react/classname';
import React from 'react';
import { getDataFromFormData } from '../../utils';
import { Button } from '../Button/Button';
import { Card } from '../Card/Card';
import { Form, ForwardedForm } from '../Form/Form';
import './Filter.scss';

const block = cn('Filter');

const defaultFilter = {
    shortTitle: '',
    description: '',
    userId: '',
    tags: [],
}

//   shortTitle — поиск регулярным выражением;
//   description — поиск регулярным выражением;
//   userId — точное совпадение;
//   tags — значение в базе данных должно включать все искомые значения.

export const Filter = ({className, onUpdate}) => {
    const [isOpen, setIsOpen] = React.useState(false);
    const form = React.useRef();

    const handleApply = (e) => {
        e.preventDefault();
        const data = getDataFromFormData(form.current);
        if (data.tags) {
            data.tags = data.tags.split(' ');
        }
        onUpdate(data)
        setIsOpen(false);
    } 

    return (
        <div className={block(null, [className])}>
            <Button onClick={() => setIsOpen(prev => !prev)}>Filter</Button>
            <Card className={block('Card', {hidden: !isOpen})}>
                <ForwardedForm className={block('Form')} onSubmit={handleApply} ref={form} onClose={() => setIsOpen(false)}>
                    <Form.TextInput label={'Short title'} name={'shortTitle'} placeholder={'Введите short title...'} />
                    <Form.TextInput label={'Description'} name={'description'} placeholder={'Введите description...'} />
                    <Form.TextInput label={'User id'} name={'userId'} placeholder={'Введите user id...'} />
                    <Form.TextInput label={'Tags'} name={'tags'} placeholder={'Введите теги через пробел...'} />
                    <Form.BtnGroup>
                        <Form.Btn className={block('ButtonLogin')} type="submit">Применить</Form.Btn>
                        <Form.Btn className={block('ButtonLogin')} view='normal' type="reset">Сбросить</Form.Btn>
                    </Form.BtnGroup>
                </ForwardedForm>
            </Card>
        </div>
    )
}