import React from 'react';
import { cn } from '@bem-react/classname';
import { useNavigate } from 'react-router';

import { Api, api } from '../../api/api';
import { Card } from '../../components/Card/Card';
import { Form, ForwardedForm } from '../../components/Form/Form';
import { Header } from '../../components/Header/Header';
import { Menu } from '../../components/Menu/Menu';
import { Layout } from '../../layouts/Layout/Layout';

import './CreateAdvertisement.scss';

const block = cn('CreateAdvertisement')

export const CreateAdvertisement = ({}) => {
    const form = React.useRef();
    const navigate = useNavigate();

    const handleSubmit = (e) => {
        e.preventDefault();
        const fd = new FormData(form.current);
        const tags = fd.get('tags');
        if (tags) {
            fd.delete('tags');
            tags.split(' ').forEach((value, i) => fd.append(`tags[${i}]`, value));
        }
 
        api.creteAdvertisement(fd)
            .then((response) => {
                if (Api.isSuccess(response)) {
                    navigate(`/advertisement/${response.data.id}`);
                }
            })
            .catch(console.error);
    }

    return <Layout>
        <Menu />
        <Card className={block()}>
            <Header>Создание объявления</Header>
            <ForwardedForm className={block('Form')} onSubmit={handleSubmit} ref={form}>
                <Form.TextInput label={'Short title'} placeholder={'Введите short title...'} name="shortTitle" required />
                <Form.TextInput label={'Description'} placeholder={'Введите description...'} name="description" />
                <Form.TextInput label={'Tags'} placeholder={'Введите теги через пробел...'} name="tags"/>
                <Form.File label={'Images'} placeholder={'Введите phone...'} name="iamges" />
                <Form.Btn className={block('ButtonCreate')} type="submit">Создать</Form.Btn>
            </ForwardedForm>
        </Card>
    </Layout>
}