import React from 'react';
import socketIOClient from "socket.io-client";

const ChatStateContext = React.createContext();
const ChatActionContext = React.createContext();
const ChatConnectContext = React.createContext();

const defaultState = {
    chats: {}
}

export function ChatProvider({children}) {
    const [state, setState] = React.useState(defaultState);
    const [ws, setWs] = React.useState(null);

    const connect = React.useCallback(() => {
        if (ws) {
            return;
        }
        const socket = socketIOClient({ query: `type=chat`, path: '/socket.io/chat' });
        socket.on("connect", () => {console.log('chat connected'); setWs(socket);});
        socket.on('disconnect', () => {console.log('chat disconnected'); setWs(null)})

        const updateChat = (data) => (prevState) => {
            const newState = {...prevState};
            newState.chats[data.id] = data;
            return newState;
        }

        socket.on("newMessage", data => {
            setState(updateChat(data));
        });

        socket.on("chatHistory", data => {
            setState(updateChat(data));
        });
    }, [ws, setWs])

    React.useEffect(() => {
        connect();
    }, [])

    const action = React.useCallback((type, data) => {
        if (!ws) {
            return;
        }
        ws.emit(type, data);
    }, [ws, setWs])

    return (
        <ChatConnectContext.Provider value={connect}>
            <ChatActionContext.Provider value={action}>
                <ChatStateContext.Provider value={state}>{children}</ChatStateContext.Provider>
            </ChatActionContext.Provider>
        </ChatConnectContext.Provider>
    );
}

function useChatState() {
    const state = React.useContext(ChatStateContext);

    if (state === undefined) {
        throw new Error('useChatState must be used within a ChatProvider');
    }

    return state;
}

function useChatAction() {
    const action = React.useContext(ChatActionContext);

    if (action === undefined) {
        throw new Error('useChatAction must be used within a ChatProvider');
    }

    return action;
}

function useChatConnect() {
    const connect = React.useContext(ChatConnectContext);

    if (connect === undefined) {
        throw new Error('useChatConnect must be used within a ChatProvider');
    }

    return connect;
}

export function useChat() {
    return {
        chat: useChatState(),
        action: useChatAction(),
        connect: useChatConnect(),
    };
}
