import React from "react";
import { cn } from "@bem-react/classname";
import { getDate } from "../../../../utils";
import { Card } from "../../../../components/Card/Card";
import { Message } from "../Message/Message";
import "./Messages.scss";

const block = cn("Messages");

function renderDate(message) {
    const date = getDate(getDateFromMessage(message));
    return <div key={date} className={block('Date', {common: true})}>{date}</div>
}

export const Messages = ({messages = [], userId}) => {
    if (!messages.length) {
        return (
            <Card className={block()}>"Нет сообщений"</Card>
        )
    }

    return (
        <Card className={block()}>
            {messages.reduce((acc, msg, idx, msgs) => {
                const elem = <Message key={msg.id} message={msg} loggedUserId={userId}/>
                
                if (needDate(msgs, idx)) {
                    acc.push(renderDate(msg))
                }
                acc.push(elem)
                return acc;
            }, [])}
        </Card>
    );
};

function getDateFromMessage(message) {
    return new Date(message?.sentAt || null);
}

function needDate(messages, index) {
    const prevDate = getDateFromMessage(messages[index - 1])
    const prevDay = prevDate.getDate();
    const prevMonth = prevDate.getMonth();

    const currDate = getDateFromMessage(messages[index])
    const day = currDate.getDate();
    const month = currDate.getMonth();

    if (month > prevMonth || day > prevDay) {
        return true;
    }
    return false;
}
