import React from "react";
import { cn } from "@bem-react/classname";
import { useNavigate } from "react-router";

import { Card } from "../../components/Card/Card";
import { Form, ForwardedForm } from "../../components/Form/Form";
import { Header } from "../../components/Header/Header";
import { Menu } from "../../components/Menu/Menu";
import { Layout } from "../../layouts/Layout/Layout";
import { getDataFromFormData } from "../../utils";
import { Api, api } from "../../api/api";

import './SignUp.scss';

const block = cn("SignUp");

export const SignUp = () => {
    const form = React.useRef();
    const navigate = useNavigate();

    const onSubmit = async (e) => {
        e.preventDefault();

        const data = getDataFromFormData(form.current);
        try {
            const response = await api.createUser(data);
            if (Api.isSuccess(response)) {
                navigate('/login');
            }
        } catch (error) {
            console.error(error)
        }
    }

    return (
        <Layout>
            <Menu />
            <Card className={block()}>
                <Header>Регистрация</Header>
                <ForwardedForm className={block('Form')} ref={form} onSubmit={onSubmit}>
                    <Form.TextInput label={'Name'} placeholder={'Введите имя...'} name="name" required />
                    <Form.TextInput label={'Email'} placeholder={'Введите email...'} name="email" required />
                    <Form.TextInput label={'Password'} placeholder={'Введите password...'} name="password" required />
                    <Form.TextInput label={'Contact phone'} placeholder={'Введите phone...'} name="contactPhone" />
                    <Form.Btn className={block('ButtonLogin')} type="submit">Создать</Form.Btn>
                </ForwardedForm>
            </Card>
        </Layout>
    );
};
