import React from 'react';
import { cn } from '@bem-react/classname';
import { useParams } from 'react-router';

import { Button } from '../../components/Button/Button';
import { Card } from '../../components/Card/Card';
import { Menu } from '../../components/Menu/Menu';
import { CHAT_ACTIONS } from '../../constants';
import { useChat, useUser } from '../../context';
import { Layout } from '../../layouts/Layout/Layout';
import { getReceiverFromChatUsers } from '../../utils';
import { Messages } from './components/Messages/Messages';

import './Chat.scss';

const block = cn('Chat');

export const Chat = () => {
    const [text, setText] = React.useState('');
    const {chat: chatState, action, connect} = useChat();
    const [userState] = useUser();
    const params = useParams();

    const inputRef = React.useRef();

    const data = chatState.chats[params.id];

    React.useEffect(() => {
        inputRef.current.focus()
    })

    React.useEffect(() => {
        connect();
    }, [connect])

    React.useEffect(() => {
        connect();
        
        if (action) {
            action(CHAT_ACTIONS.getHistory, params.id);
        }
    }, [action])

    const onSubmit = () => {
        if (data && text) {
            action(CHAT_ACTIONS.sendMessage, {
                users: data.users.map(user => user.id),
                text,
            });
            setText('')
        }
    }

    const onKeyDown = (e) => {
        if (e.code === 'Enter') {
            onSubmit();
        }
    }

    return (
        <Layout>
            <Menu />
            <Card className={block()}>
                <div className={block("Content")}>
                    <Card className={block("Header")}>
                        {data ? getReceiverFromChatUsers(data.users, userState.id).name : "Собеседник"}
                    </Card>
                    <Messages messages={data?.messages} userId={userState.id}/>
                    <div className={block("Actions")}>
                        <input
                            ref={inputRef}
                            className={block("Input")}
                            type='text'
                            name={"message"}
                            value={text}
                            onChange={(e) => setText(e.target.value)}
                            onKeyDown={onKeyDown}
                            autoComplete={'off'}
                        />
                        <Button
                            className={block("BtnSend")}
                            onClick={onSubmit}
                            pin='brick-brick'
                        >
                            Отправить
                        </Button>
                    </div>
                </div>
            </Card>
        </Layout>
    );
}