import React from "react";
import { cn } from "@bem-react/classname";

import "./Form.scss";
import { Link } from "react-router-dom";
import { Button } from "../Button/Button";

const block = cn("Form");

export function Form({ className, onSubmit, onClose, children }, ref) {
    return (
        <form
            className={block(null, [className])}
            onSubmit={onSubmit}
            ref={ref}
        >   
            {onClose && <button className={block('Close')} onClick={onClose} type={'button'}>х</button>}
            {children}
        </form>
    );
}

export const ForwardedForm = React.forwardRef(Form);

Form.TextInput = function TextInput({
    className,
    label,
    value,
    onChange,
    name,
    required = false,
    placeholder,
    id,
    autoComplete = 'off'
}) {
    const renderInput = () => {
        if (onChange !== undefined) {
            return (
                <input
                    className={block("Input")}
                    type='text'
                    name={name}
                    value={value}
                    onChange={onChange}
                    required={required}
                    placeholder={placeholder}
                    id={id}
                    autoComplete={autoComplete}
                />
            );
        } else {
            return (
                <input
                    className={block("Input")}
                    type='text'
                    name={name}
                    required={required}
                    placeholder={placeholder}
                    id={id}
                    autoComplete={autoComplete}
                />
            );
        }
    };

    return (
        <div className={block("TextInput", [className, block("Field")])}>
            {label && (
                <label className={block("Label")} htmlFor={name}>
                    {label}
                    {required && <span className={block("Required")}>*</span>}
                </label>
            )}
            {renderInput()}
        </div>
    );
};

Form.File = function Files({
    label,
    name,
    multiple = true,
    required,
    placeholder,
}) {
    return (
        <div className={block("FileInput", [block("Field")])}>
            {label && (
                <label className={block("Label")} htmlFor={name}>
                    {label}
                    {required && <span className={block("Required")}>*</span>}
                </label>
            )}
            <input
                type='file'
                name='images'
                multiple={multiple}
                placeholder={placeholder}
            />
        </div>
    );
};

Form.BtnGroup = function Btn(props) {
    return <div className={block('BtnGroup')}>{props.children}</div>;
};

Form.Btn = function Btn(props) {
    return <Button {...props} />;
};
