import { cn } from "@bem-react/classname";
import React from "react";
import { useNavigate } from "react-router";
import { Api, api } from "../../api/api";
import { useUser } from "../../context/user";
import { Advertisement } from "../Advertisement/Advertisement";
import { Button } from "../Button/Button";
import { Card } from "../Card/Card";
import { Filter } from "../Filter/Filter";
import { Header } from "../Header/Header";
import { Search } from "../Search/Search";

import "./Advertisements.scss";

const block = cn('Advertisements');

export const Advertisements = () => {
    const [advertisements, setAdvertisements] = React.useState([]);
    const [filteredAdvertisements, setFilteredAdvertisements] = React.useState([]);
    const [filterText, setFilterText] = React.useState('');
    const [user] = useUser();

    React.useEffect(() => {
        api.getAdvertisements()
            .then((response) => {
                if (response.data) {
                    setAdvertisements(response.data);
                }
            })
            .catch(console.error);
    }, []);

    React.useEffect(() => {
        setFilteredAdvertisements(
            advertisements.filter(advertisement => {
                const shortTitle = advertisement.shortTitle.toLowerCase();
                const lowerFilterText = filterText.toLowerCase();

                return shortTitle.includes(lowerFilterText);
            })
        )
    }, [advertisements, filterText])

    const updateFilter = async (filter) => {
        try {
            const response = await api.getAdvertisements(filter);
            if (Api.isSuccess(response)) {
                setAdvertisements(response.data);
            }
        } catch (err) {
            console.error(err);
        }
    }


    return (
        <Card className={block()}>
            <Header>Объявления</Header>
            <div className={block('Panel')}>
                <Search className={block('Search')} label="Фильтр по названию" onUpdate={setFilterText} />
                <Filter className={block('Filter')} onUpdate={updateFilter} />
            </div>
            <div className={block('Content')}>
                {filteredAdvertisements.map((item) => (
                    <Advertisement key={item.id} {...item} className={block('Advertisement')}/>
                ))}
            </div>
            {user.isAuth && <div className={block('Actions')}>
                <Button className={block('BtnCreate')} to={'/create-advertisement'} view={'third'}>Создать объявление</Button>
            </div>}
        </Card>
    );
};
