import { cn } from "@bem-react/classname";
import React from "react";
import { Link } from "react-router-dom";
import "./Button.scss";

const block = cn("Button");

export const Button = ({
    className,
    children,
    type = "button",
    to,
    view,
    pin = 'round-round',
    onClick = () => {},
}) => {
    if (to) {
        return (
            <Link className={block({link: true, view, pin})} to={to}>
                {children}
            </Link>
        );
    }

    return (
        <button
            className={block({view, pin}, [className])}
            type={type}
            onClick={onClick}
        >
            {children}
        </button>
    );
};
