export function getDataFromFormData(form) {
    const fd = new FormData(form);
    return Object.fromEntries(fd.entries());
}

function getFilledDataFromObject(data = {}) {
    return Object.entries(data).reduce((acc, [k, v]) => {
        if (v) {
            acc[k] = v;
        }
        return acc;
    }, {});
}

export function getQueryFromObject(data) {
    const query = new URLSearchParams(getFilledDataFromObject(data));
    return query.toString();
}

export function getStaticUrl(path) {
    return `${window.location.origin}/${path}`;
}

export function getReceiverFromChatUsers(users, loggedUser) {
    return users.filter(user => user.id !== loggedUser)[0];
}

export function getTime(str) {
    return new Date(str).toLocaleString("ru-Ru", {
        hour: "2-digit",
        minute: "2-digit",
    })
}

export function getDate(str) {
    return new Date(str).toLocaleString("ru-Ru", {
        day: "2-digit",
        month: "2-digit",
        year: "numeric",
    })
}

export function getDateTime(str) {
    return new Date(str).toLocaleString("ru-Ru", {
        hour: "2-digit",
        minute: "2-digit",
        day: "2-digit",
        month: "2-digit",
        year: "numeric",
    })
}

function getLastItem(array) {
    return array[array.length - 1];
}

export function sortChatByMessage(a, b) {
    return new Date(getLastItem(b.messages)?.sentAt) - new Date(getLastItem(a.messages)?.sentAt)
}