import React from "react";
import { cn } from "@bem-react/classname";

import "./Message.scss";
import { getTime } from "../../../../utils";

const block = cn("Message");

export const Message = ({ className, message, loggedUserId }) => {
    const msgRef = React.useRef();
    React.useEffect(() => {
        msgRef.current.scrollIntoView();
    }, []);

    const sentAt = getTime(message.sentAt);
    return (
        <div
            ref={msgRef}
            className={block({ my: message.author === loggedUserId }, [className])}
        >
            {message.text}
            <span className={block("Date")} text={sentAt}>
                {sentAt}
            </span>
        </div>
    );
    
    
    // <div ref={msgRef} className={block(null, [className])}>Message</div>;
};
