import React from "react";
import { cn } from "@bem-react/classname";
import { useNavigate, useParams } from "react-router-dom";

import { Api, api } from "../../api/api";
import { Card } from "../../components/Card/Card";
import { Header } from "../../components/Header/Header";
import { Menu } from "../../components/Menu/Menu";
import { Layout } from "../../layouts/Layout/Layout";
import { getStaticUrl } from "../../utils";
import { useUser } from "../../context";
import { Button } from "../../components/Button/Button";
import { Modal } from "../../components/Modal/Modal";

import "./AdvertisementPage.scss";

const block = cn("AdvertisementPage");

export const AdvertisementPage = ({}) => {
    const [adv, setAdv] = React.useState(null);
    const [open, setOpen] = React.useState(false);
    const params = useParams();

    const [userState] = useUser();
    const navigate = useNavigate();

    const handleCreateChat = async () => {

        const response = await api.createChat({receiver: adv.user.id});

        if (Api.isSuccess(response)) {
            navigate(`/chats/${response.data.id}`)
        }
    }

    const handleDeleteAdv = async () => {
        const response = await api.deleteAdvertisement(adv.id);

        if (Api.isSuccess(response)) {
            navigate('/');
        }
    }

    React.useEffect(() => {
        if (!params.id) {
            return;
        }
        api.getAdvertisement(params.id)
            .then((response) => {
                if (Api.isSuccess(response)) {
                    setAdv(response.data);
                }
            })
            .catch(console.error);
    }, [params.id]);

    const actions = !userState.isAuth ? null : (
        <div className={block('Actions')}>
            <Button onClick={handleCreateChat}>
                Начать чат
            </Button>
            {userState.id === adv?.user.id && <Button onClick={() => setOpen(true)} view='outlined-danger'>
                Удалить объявление
            </Button>}
        </div>
    )

    return (
        <Layout>
            <Menu />
            <Card className={block()}>
                <Header className={block('Header')} actions={actions}>Объявление</Header>
                {!adv ? (
                    <div>Нет данных</div>
                ) : (
                    <div className={block("Wrap")}>
                        <div className={block("ImagesBlock")}>
                            <h2 className={block("BlockTitle")}>Изображения</h2>
                            <div className={block("ImageWrap")}>
                                {adv.images.map((url) => (
                                    <img
                                        className={block("Image")}
                                        key={url}
                                        src={getStaticUrl(url)}
                                        alt={"img"}
                                    />
                                ))}
                            </div>
                        </div>
                        <div className={block("Content")}>
                            <div className={block("Block", [])}>
                                <h2 className={block("BlockTitle")}>Контакты</h2>
                                <address className={block('Address')}>
                                    <div>{adv.user.name}</div>
                                    {adv.user.contactPhone && <div>{adv.user.contactPhone}</div>}
                                </address>
                            </div>
                            <div className={block("Block")}>
                                <h2 className={block("BlockTitle")}>Title</h2>
                                <p className={block("Text")}>{adv.shortTitle}</p>
                            </div>
                            <div className={block("Block")}>
                                <h2 className={block("BlockTitle")}>
                                    Description
                                </h2>
                                <p className={block("Text")}>{adv.description}</p>
                            </div>
                            {Boolean(adv?.tags?.length) && (
                                <div className={block("Block")}>
                                    <h2 className={block("BlockTitle")}>
                                        Tags
                                    </h2>
                                    <ul className={block("List")}>
                                        {adv.tags.map((tag, i) => (
                                            <li key={i} className={block("ListItem")}>
                                                #{tag}
                                            </li>
                                        ))}
                                    </ul>
                                </div>
                            )}
                        </div>
                    </div>
                )}
                <Modal open={open}>
                    <Modal.Header onClose={() => setOpen(false)}>Подтвердите удаление</Modal.Header>
                    <Modal.Footer onAccept={handleDeleteAdv} onDecline={() => setOpen(false)} />
                </Modal>
            </Card>
        </Layout>
    );
};
