import { cn } from "@bem-react/classname";
import React from "react";

import { useLocation, useNavigate } from "react-router-dom";
import { Api, api } from "../../api/api";
import { useUser } from "../../context";
import { Button } from "../Button/Button";
import "./Menu.scss";

const block = cn("Menu");

export const Menu = (props) => {
    const [userState, userUpdate] = useUser();

    const navigate = useNavigate();
    const location = useLocation();

    React.useEffect(() => {
        userUpdate()
            .catch(console.error);
    }, [location.pathname]);

    const onLogout = async () => {
        const response = await api.logout();
    
        if (Api.isSuccess(response)) {
            navigate('/login');
        }
    }

    return (
        <div className={block()}>
            <div className={block("Left", [block("Group")])}>
                <Button to='/' view={'flat'}>Home</Button>
            </div>
            <div className={block("Center", [block("Group")])}>
                {props.children}
            </div>
            <div className={block("Right", [block("Group")])}>
                {userState.isAuth && (
                    <div className={block('Profile')}> 
                        <span>{userState.name}</span>
                        <Button to={"/profile"}>Profile</Button>
                    </div>
                )}
                {userState.isAuth && <Button className={block('Chats')} to={"/chats"}>Chats</Button>}
                {userState.isAuth ? 
                    <Button className={block('BtnAuth')} onClick={onLogout}>Logout</Button> :
                    <Button className={block('BtnAuth')} to={"/login"}>Login</Button>
                }
            </div>
        </div>
    );
};
