import React from "react";
import { cn } from "@bem-react/classname";

import "./Header.scss";

const block = cn('Header');

export const Header = ({ className, children, level = 1, actions }) => {
    let HeaderTag = `h${level}`;

    return (
        <HeaderTag className={block(null, [className])}>
            <span>{children}</span>
            {actions && actions}
        </HeaderTag>
    );
};
