import React from "react";
import { cn } from "@bem-react/classname";
import { useNavigate } from "react-router-dom";

import { Api, api } from "../../api/api";
import { Form, ForwardedForm } from "../../components/Form/Form";
import { Layout } from "../../layouts/Layout/Layout";
import { getDataFromFormData } from "../../utils";

import "./Login.scss";

const block = cn('Login');

export const Login = () => {
    const form = React.useRef();
    const navigate = useNavigate();

    const handleSubmit = async (e) => {
        e.preventDefault();

        const data = getDataFromFormData(form.current);

        try {
            const response = await api.login(data);
            if (Api.isSuccess(response)) {
                navigate("/");
            }
        } catch (error) {
            console.error(error);
        }
    };

    return (
        <Layout verticalAlignCenter>
            <ForwardedForm className={block()} onSubmit={handleSubmit} ref={form}>
                <Form.TextInput label={'Email'} name={'email'} placeholder={'Введите email...'} autoComplete required/>
                <Form.TextInput label={'Password'} name={'password'} placeholder={'Введите password...'} required/>
                <Form.Btn className={block('ButtonLogin')} type="submit">Войти</Form.Btn>
                <Form.Btn to={'/signup'} view="flat">Зарегистрироваться</Form.Btn>
            </ForwardedForm>
        </Layout>
    );
};
